.store-selection-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
}

.store-selection-header {
  text-align: center;
  margin-bottom: 40px;
}

.store-selection-header h1 {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 10px;
}

.store-selection-header p {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 20px;
}

.back-button {
  padding: 10px 20px;
  font-size: 1rem;
  color: #667eea;
  background: transparent;
  border: 2px solid #667eea;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.back-button:hover {
  background: #667eea;
  color: white;
}

.store-selection-form {
  background: white;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.stores-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}

.store-card {
  padding: 20px;
  border: 2px solid #e0e0e0;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: block;
}

.store-card:hover {
  border-color: #667eea;
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.2);
  transform: translateY(-2px);
}

.store-card.selected {
  border-color: #667eea;
  background: linear-gradient(135deg, #f8f9ff 0%, #fff 100%);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.store-radio {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.store-info {
  pointer-events: none;
}

.store-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.store-icon {
  font-size: 2rem;
}

.store-name {
  font-size: 1.3rem;
  font-weight: 600;
  color: #333;
  margin: 0;
  flex: 1;
}

.selected-badge {
  padding: 4px 12px;
  background: #4caf50;
  color: white;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 600;
}

.store-address {
  color: #666;
  margin: 8px 0;
  font-size: 0.95rem;
}

.store-distance {
  color: #888;
  margin: 8px 0;
  font-size: 0.9rem;
}

.store-type-badge {
  display: inline-block;
  padding: 4px 12px;
  background: #e8eaf6;
  color: #5c6bc0;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 500;
  margin-top: 8px;
}

.no-stores {
  text-align: center;
  padding: 60px 20px;
}

.no-stores p {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 20px;
}

.retry-button {
  padding: 12px 24px;
  font-size: 1rem;
  color: white;
  background: #667eea;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.retry-button:hover {
  background: #5568d3;
  transform: translateY(-2px);
}

.error-message {
  margin: 20px 0;
  padding: 12px;
  background: #fff3f3;
  border-left: 4px solid #ff4444;
  border-radius: 8px;
  color: #cc0000;
  font-weight: 500;
}

.continue-button {
  width: 100%;
  padding: 18px;
  font-size: 1.2rem;
  font-weight: 600;
  color: white;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.continue-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.continue-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.arrow {
  font-size: 1.3rem;
  transition: transform 0.3s ease;
}

.continue-button:hover:not(:disabled) .arrow {
  transform: translateX(4px);
}

.store-info-box {
  margin-top: 30px;
  padding: 20px;
  background: #f8f9ff;
  border-radius: 12px;
  border-left: 4px solid #667eea;
}

.store-info-box p {
  margin: 8px 0;
  color: #555;
  line-height: 1.6;
}

.store-info-box strong {
  color: #333;
}

/* Responsive */
@media (max-width: 768px) {
  .store-selection-container {
    padding: 20px 15px;
  }

  .store-selection-header h1 {
    font-size: 2rem;
  }

  .store-selection-form {
    padding: 20px;
  }

  .store-card {
    padding: 15px;
  }

  .store-name {
    font-size: 1.1rem;
  }

  .store-header {
    flex-wrap: wrap;
  }
}
